<template>
	<div class="search">
		<div class="box">
			<!-- 搜索框 -->
			<div class="shang">
				<div class="xbox">
					<div class="tp">
						<img src="../../public/wyyimg/sousuo.png" mode="">
					</div>
					<input type="text" v-model="searchWord" placeholder="搜索歌手,	歌曲,专辑" />
					<div class="delete">
						<img v-if="searchWord !== ''" src="../../public/wyyimg/shangcu.png" alt="" @click="value_input()">
					</div>
				</div>
			</div>
			<!-- 热门搜索 -->
			<div class="xia" v-if="searchWord == ''">
				<div class="hot">
					<span>
						热门搜索
					</span>
				</div>
				<ul>
					<li v-for="(item,index) in list" :key="index">
						<span>{{item.first}}</span>
					</li>
				</ul>
			</div>
			
			<!-- 搜索列表 -->
			<div class="listings" v-else="false">
				<div class="hots">
					<span>
						搜索"{{searchWord}}"
					</span>
				</div>
				<ul>
					<li @click="goSta(item)" v-for="(item,index) in suggestWords" :key="index">
						<div class="tps">
							<img src="../../public/wyyimg/sousuo.png" mode=""></image>
						</div>
						<div class="searchsong">
							<span>{{item.name}}</span>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data() {
			return {
				locda:[],
				data:[],
				list:[],
				searchWord: '',
				suggestWords: [],
			};
		},
// 		mounted:{
// 			
// 		},
		methods:{
			go(item) {
				this.$router.push({name:'player',params:{a:item.al.picUrl}})
			},
			goSta(item){
				//本地缓存
			var tiem = new Date().getTime();
            localStorage.setItem('ls'+tiem,JSON.stringify(item.name))
				console.log(item.name)
			},
			//请求搜索api
			getsearchWord:function(){
				// axios.get('/api/search?keywords='+ this.searchWord)
				axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/search/suggest?keywords='+ this.searchWord)

				.then(res => {
					this.suggestWords = res.data.result.songs.slice(0,6)
					console.log(this.suggestWords)
					// console.log(res)
				})
				.catch(err => {
					console.log("请求失败" + err)
				})
			},
			
			//input框设置为空
			value_input(){
				this.searchWord = '';
			}
		},
		watch:{
			searchWord:function(){
				// console.log("xxx")
				this.getsearchWord()
				
			}
		},
		created(){
			// 取所有的缓存
			for(var key in window.localStorage){
   					var r =  new RegExp('ls','ig')
    		    	if(r.test(key)){ 
						// console.log(key)
                     var x = JSON.parse(window.localStorage.getItem(key));  
                      // console.log('ls',x)  
                      this.data.unshift(x)
					  this.locda.unshift(key)
                      
                      
    		    	}
    		   }	
					// 去重
			  for(var i =0;i<this.data.length;i++){
	       		   for(var j = i+1;j<this.data.length;j++){
	      			if(this.data[i]===this.data[j]){
	      				this.data.splice(j,1);
	      				j--;
	      				}
	      			}
	      		}
					//删除本地缓存
				if(this.locda.length>10){
				   	for(var z=10;z<this.locda.length;z++){
				   		console.log('sdfsdfsdf s',this.locda[z])
				   		localStorage.removeItem(this.locda[z])
				}
			}
			// console.log(this.data.splice(0,10))
			
			//请求热门搜索
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/search/hot')
			.then(res => {
				this.list = res.data.result.hots
				console.log(this.list)
			})
			.catch(err => {
				console.log("请求失败" + err)
			})
		}
	}
</script>

<style scoped lang="less">
	.search{
		.box{
			width: 100%;
			background-color: #FFFFFF;
			overflow: hidden;
			.shang{
				width: 355px;
				height: 30px;
				padding: 15px 10px;
				background-color: #FBFCFD;
				border-bottom: 1px solid #F1F2F3;
				.xbox{
					position: relative;
					width: 100%;
					height: 30px;
					border-radius:20px;
					background-color: #EBECEC;
					.tp{
						float: left;
						margin-left: 4px;
						margin-top: 4px;
						width: 23px;
						height: 23px;
						img{
							width: 100%;
						}
					}
					input{
						width: 311px;
						height: 17px;
						float: left;
						margin-top: 5px;
						margin-left:5px ;
						border:none ;
						outline: none;
						background-color: #EBECEC ;
						font-size: 14px;
						&::-webkit-input-placeholder{
						color: #C9C9C9; 
						}
					}
					.delete{
						position: absolute;
						right: 8px;
						top: 4px;
						width: 13px;
						height: 13px;
						img{
							width: 100%;
						}
					}
				}
			}
			.xia{
				width: 100%;
				padding: 15px 10px;
				background-color: #FBFCFD;
				.hot{
					width: 100%;
					font-size: 12px;
					color: #666666;
				}
				ul{
					width: 355px;
					padding: 0;
					list-style: none;
					overflow: hidden;
					margin: 10px 0 7px 0;
					li{
						float: left;
						height: 31px;
						margin-right: 8px;
						margin-bottom: 8px;
						padding: 0 14px;
						font-size:14px ;
						line-height: 31px;
						border:1px solid #d3d4da;
						border-radius:32px ;
					}
				}
			}
			.listings{
				width: 100%;
				overflow: hidden;
				.hots{
					height: 50px;
					margin-left: 10px;
					padding-right:10px ;
					font-size: 15px;
					line-height: 50px;
					color: #507daf;
					border-bottom: 1px solid #F1F2F3;
				}
				ul{
					width: 100%;
					padding: 0;
					list-style: none;
					overflow: hidden;
					margin: 0px;
					li{
						position:relative;
						height: 45px;
						padding-left:10px ;
						.tps{
							position: absolute;
							width: 25px;
							height: 25px;
							left:0;
							top:0;
							padding:10px 0px 10px 10px;
							img{
								width: 100%;
							}
						}
						.searchsong{
							position: absolute;
							left: 35px;
							top: 0;
							width: 100%;
							height: 44px;
							line-height: 44px;
							padding-right: 10px;
							font-size: 15px;
							white-space:nowrap;
							overflow:hidden;
							text-overflow:ellipsis;
							max-width: 305px;
							border-bottom: 1px solid #f1f2f3;
						}
					}
				}
			}
		}
	}
</style>
